'use strict';

var Caml_obj = require("bs-platform/lib/js/caml_obj.js");
var Belt_Array = require("bs-platform/lib/js/belt_Array.js");
var Caml_array = require("bs-platform/lib/js/caml_array.js");
var Caml_option = require("bs-platform/lib/js/caml_option.js");

var Types_ = { };

function getVariantsWith(variants, option) {
  return Belt_Array.keep(variants, (function ($$var) {
                return Belt_Array.some($$var.selectedOptions, (function (o) {
                              return Caml_obj.caml_equal({
                                          name: o.name,
                                          value: o.value
                                        }, option);
                            }));
              }));
}

function getSelectedItemFromVariants(variants, options) {
  return Caml_array.caml_array_get(Belt_Array.reduce(options, variants, getVariantsWith), 0);
}

function saveCheckoutId(id) {
  localStorage.setItem("checkout_id", id);
  
}

function getCheckout(client) {
  var id = localStorage.getItem("checkout_id");
  if (id !== null) {
    return client.checkout.fetch(id === null ? undefined : Caml_option.some(id));
  } else {
    return client.checkout.create().then((function (checkout) {
                  saveCheckoutId(checkout.id);
                  return Promise.resolve(checkout);
                }));
  }
}

var Utils = {
  getVariantsWith: getVariantsWith,
  getSelectedItemFromVariants: getSelectedItemFromVariants,
  saveCheckoutId: saveCheckoutId,
  getCheckout: getCheckout
};

exports.Types_ = Types_;
exports.Utils = Utils;
/* No side effect */
