'use strict';

var React = require("react");
var ReactLottie = require("react-lottie");
var LoadingJson = require("../assets/lotties/loading.json");

function str(prim) {
  return prim;
}

var loadingAnimation = LoadingJson;

function Loading(Props) {
  var animationOptions_rendererSettings = {
    preserveAspectRatio: "xMidYMid slice"
  };
  var animationOptions = {
    loop: true,
    autoplay: true,
    animationData: loadingAnimation,
    rendererSettings: animationOptions_rendererSettings
  };
  return React.createElement(ReactLottie.default, {
              options: animationOptions,
              width: 25,
              height: 25
            });
}

var make = Loading;

exports.str = str;
exports.loadingAnimation = loadingAnimation;
exports.make = make;
